body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

@font-face {
  font-family: 'Open Sans', arial;
  font-family: 'Open Sans Extrabold', arial;
  src: url("../font/open-sans.ttf") format("ttf");
  src: url("../font/open-sans-extrabold.ttf") format("ttf");
}

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  padding-left: 18px;
  padding-right: 18px;
}

.header {
  background: #f6f2f2;
  font-family: 'Open Sans';
}

.header__nav {
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
}

.header__ul {
  display: flex;
}

.header__li {
  color: #e0e0e0;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  padding: 44px 21px 35px 21px;
  line-height: 1;
}

.header__li:hover {
  background: #ff3f40;
  cursor: pointer;
}

.header__content {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
  padding-top: 108px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
  justify-content: space-between;
}

.block {
  width: calc(50% - 2%);
}

.block__title {
  text-transform: uppercase;
  font-family: 'Open Sans Extrabold', arial;
  font-size: 60px;
}

.block__text {
  margin-top: 36px;
  color: rgba(0, 0, 0, 0.7);
}

.block__button {
  margin-top: 64px;
  margin-bottom: 123px;
  width: 227px;
  height: 55px;
  background: #ff2d2d;
  color: #fff;
}

.big-btn {
  border-style: none;
  font-weight: bold;
  font-size: 18px;
  text-transform: capitalize;
  border-radius: 5px;
  cursor: pointer;
}

.youtube-block {
  width: calc(50% - 2%);
  display: flex;
  justify-content: flex-end;
}

.youtube-block__item {
  width: 536px;
}

.li-link {
  color: inherit;
  text-decoration: none;
}

.flexible-block {
  position: relative;
}

.flexible-block_video {
  background: #ff2222;
  max-width: inherit;
  box-shadow: -4px -14px 73px rgba(0, 0, 0, 0.29);
}

.flexible-block_product {
  max-width: inherit;
}

.flexible-block__ratio {
  height: 0;
}

.flexible-block__ratio_16-9 {
  padding-top: 56.25%;
}

.flexible-block__ratio_1-1 {
  padding-top: 100%;
}

.flexible-block__content {
  border: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-link {
  width: inherit;
  height: inherit;
}

.picture {
  background: #f5f0dd;
  width: inherit;
  height: inherit;
}

.play-video {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -35px;
  margin-top: -34.5px;
}

.shadow-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity, 0.4s;
}

.shadow-link:hover {
  background: rgba(0, 0, 0, 0.29);
  cursor: pointer;
}

.provide {
  padding-top: 110px;
}

.provide__block {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
