  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
  padding-bottom: 112px;
  border-bottom: 2px #ececec solid;
  font-family: 'Open Sans', arial;
  justify-content: space-between;
}

.title-services {
  width: calc(41.66666666666667% - 2%);
}

.title-services__text {
  margin-top: 50px;
}

.title-services__button {
  width: 227px;
  height: 55px;
  margin-top: 61px;
  color: #ff2d2d;
  background: inherit;
  border: 2px #ff2d2d solid;
}

.services {
  font-family: 'Open Sans', arial;
  width: calc(58.333333333333336% - 2%);
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
  color: rgba(0, 0, 0, 0.7);
  justify-content: space-around;
}

.services__item {
  width: calc(50% - 2%);
  margin-bottom: 51px;
}

.services__title {
  margin-top: 32px;
  font-size: 24px;
  font-weight: 600;
}

.services__text {
  margin-top: 19px;
  font-size: 16px;
