}

.block-aside {
  width: calc(66.66666666666667% - 2%);
}

.description {
  width: calc(83.33333333333334% - 2%);
}

.description__title {
  font-size: 30px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.8);
  text-transform: uppercase;
}

.description__text {
  margin-bottom: 52px;
}

.contacts {
  display: flex;
}

.contacts__li {
  margin-right: 40px;
}

.contacts__link {
  text-decoration: none;
  font-style: italic;
  color: rgba(0, 0, 0, 0.8);
}

.mini-photo-block {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
