}

.name {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  opacity: 0;
}

.name:hover {
  opacity: 1;
  cursor: pointer;
}

.name__p {
  width: 100%;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background: #7a786e;
  padding: 8px 0;
  font-size: 18px;
  font-weight: 600;
  margin-top: auto;
}

.photo-hover {
  cursor: pointer;
}

.subscribe {
  padding-top: 145px;
  padding-bottom: 140px;
  background: #f5f0dd;
  font-family: 'Open Sans', arial;
}

.subscribe__title {
  text-align: center;
  font-size: 42px;
  font-weight: bold;
}

.subscribe__subtitle {
  text-align: center;
  padding-top: 41px;
}

.subscribe-form {
  font-family: 'Open Sans', arial;
  text-align: center;
  margin-top: 66px;
  height: 80px;
}

.subscribe-form__email {
  width: 523px;
  height: 100%;
  padding-left: 43px;
