}

.feedback {
  font-family: 'Open Sans', arial;
  padding: 110px 0;
}

.feedback__block {
  width: calc(50% - 2%);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
}

.feedback-block {
  position: relative;
}

.feedback-block-bg {
  position: absolute;
