}

.feedback-form__text {
  margin-top: 51px;
  padding: 36px 54px;
  width: 100%;
  border-radius: 5px;
  border-style: none;
}

.feedback-form_bg_dark {
  background: #e5e5e5;
}

.input-huge {
  font-size: 18px;
  width: 336px;
  box-sizing: border-box;
  text-transform: capitalize;
  padding: 0 52px;
